"use client";

import React, { useState } from "react";

export default function ContactForm() {
  const [email, setEmail] = useState("");
  const [subscribe, setSubscribe] = useState(false);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // For now, just alert the input values (no backend)
    alert(
      "Email: " + email + "\\nSubscribed to newsletter: " + (subscribe ? "Yes" : "No")
    );
  };

  return (
    <section className="max-w-7xl mx-auto px-6 py-16">
      <h2 className="text-3xl font-semibold mb-6">Get in Touch</h2>
      <p className="mb-6 text-gray-700">
        Have a question or want to learn more? Reach out to us and we'll get
        back to you as soon as possible.
      </p>
      <form
        onSubmit={handleSubmit}
        className="bg-gray-100 p-6 rounded-lg max-w-xl"
      >
        <label htmlFor="email" className="block mb-2 font-medium">
          Email *
        </label>
        <input
          type="email"
          id="email"
          required
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          className="w-full p-3 rounded border border-gray-300 mb-4 focus:outline-none focus:ring-2 focus:ring-blue-500"
          placeholder="you@example.com"
        />
        <div className="flex items-center mb-6">
          <input
            type="checkbox"
            id="subscribe"
            checked={subscribe}
            onChange={(e) => setSubscribe(e.target.checked)}
            className="mr-2"
          />
          <label htmlFor="subscribe" className="text-gray-700">
            Yes, subscribe me to your newsletter.
          </label>
        </div>
        <button
          type="submit"
          className="w-full bg-blue-600 text-white py-3 rounded hover:bg-blue-700 transition"
        >
          Submit
        </button>
      </form>
    </section>
  );
}
